.sched {
    padding: 70px 0 0; }

.sched__title {
    @include title; }

.sched__inner {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center; }

.sched__item {
    position: relative;
    text-align: center;
    width: 35%; }

.sched__item-wrap {
    padding: 10px;
    transition: box-shadow .15s linear;
    min-height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }

.sched__item-icon {
    fill: $color-primary-light;
    width: 80px;
    height: 80px;
    margin-bottom: 15px; }

.sched__item-link {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0; }

.sched__item-link:hover ~ .sched__item-wrap {
    box-shadow: 0 0 20px rgba(0, 0, 0, .15); }
