@mixin title {
    font-size: 35px;
    font-family: $font-title;
    text-align: center;
    color: $color-secondary-dark;
    text-transform: uppercase;
    margin-bottom: 60px;
    @media only screen and (max-width: 767px) {
        font-size: 28px;
        margin-bottom: 40px; }
    @media only screen and (max-width: 425px) {
        font-size: 24px;
        margin-bottom: 30px; } }

@mixin subtitle {
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 20px; }

@mixin list {
    &:not(:last-child) {
        margin-bottom: 30px; } }

@mixin list-item {
    font-size: 16px;
    &:not(:last-child) {
        margin-bottom: 15px; }
    a {
        color: $color-primary-light;
        &:hover {
            text-decoration: underline; } } }

@mixin icon {
    fill: $color-tertiary-dark;
    height: 20px;
    width: 20px;
    margin-right: 5px;
    height: 15px;
    width: 15px; }

@mixin default-link {
    color: $color-primary-light;
    &:hover {
        text-decoration: underline; } }

@mixin btn {
    color: $color-primary-dark;
    font-family: $font-title;
    padding: 15px 55px;
    transition: background-color .1s linear, color .1s linear;
    border: 1px $color-secondary-dark solid;
    border-radius: 100px;
    background-color: $color-secondary-dark;

    &:hover {
        transition: background-color .1s linear, color .1s linear;
        background-color: darken($color-secondary-dark, 7);
        border-color: darken($color-secondary-dark, 7);
        color: $color-secondary-light; } }

@mixin img-filter {
    display: block;
    position: absolute;

    width: 100%;
    height: 100%;

    content: '';

    opacity: .4;
    background-color: transparentize($color-primary-dark, .3); }

