@media only screen and (max-width: 525px) {
    .cts__block {
        max-width: 100%;
        width: 100%;
        text-align: center;
        padding: 0; }

    .cts__container {
        margin: 0; }

    .cts__list-item {
        flex-direction: column-reverse; }

    .cts__phone-desc {
        margin: 0;
        line-height: normal;
        &:not(:nth-child(1)) {
            margin-top: 10px; } }

    .cts__address {
        margin: auto; }

    .cts__map-container {
        height: 400px; } }

@media screen and (orientation:landscape) and (max-width: 767px) {
    .cts__map-container {
        width: 90%;
        border-right: 2px solid $color-primary-light;
        position: relative;
        &:before {
            content: 'Прокрутка';
            top: 50%;
            right: -57px;
            position: absolute;
            transform: rotate(-90deg) translateX(25%); } } }
