.nav {
    position: relative;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    &::before {
        @include img-filter; } }

.nav__inner {
    position: relative;

    z-index: 2;

    padding: 40px 15px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    min-height: 380px; }

.nav__title {
    text-transform: uppercase;
    font-family: $font-title;
    color: $color-secondary-light;
    font-size: 65px;
    text-align: center;
    font-weight: 600;
    max-width: 1280px;
    width: 100%;
    text-shadow: 1px 1px 2px #000;
    margin-bottom: 20px; }

.nav__items {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap; }

.nav__link {
    color: #c0bebe;
    text-transform: uppercase;
    font-family: $font-title;
    font-size: 18px;

    transition: color .1s linear; }

.nav__link:not(:last-child)::after {
    content: '/';
    margin: 0 5px; }

.nav__link:hover {
    color: $color-secondary-light; }

.nav__link--active {
    color: $color-secondary-light; }

@media only screen and (max-width: 1079px) {
    .nav__inner {
        min-height: 270px; }

    .nav__title {
        font-size: 35px; }

    .nav__link {
        font-size: 16px; } }

@media only screen and (max-width: 575px) {
    .nav__title {
        font-size: 30px; } }

@media only screen and (max-width: 480px) {
    .nav__title {
        font-size: 25px; } }
